<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "change-component",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      qryBtnDisabled: false, //查詢按鈕停用與否
      updateBtnDisabled: false, //修改按鈕停用與否
      allCourseData: [], //所有承攬商入廠施工群組資料
      allPersonListForDutyData: [], //該承攬商入廠施工群組的所有安衛與監工人員資料
      dailyProjectData: [], //單據列表資料
      selectedData: null, //已選擇的施工單號
      formQuery: {
        course: null, //承攬商入廠施工群組
        applyPk: "", //承攬商申請單號
      },
      formSend: {
        selectedData: {}, //已選擇的施工單號
        chgDutyPerson: null, //欲變更的安全人員資料
      },
      rulesQuery: {
        course: [{ required: true, message: "必填", trigger: "change" }],
        applyPk: [{ required: true, message: "必填", trigger: "change" }],
      },
      rulesSend: {
        chgDutyPerson: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "applyPk",
          label: "申請單號",
        },
        {
          id: "projectName",
          label: "工程編號/工程名稱",
          width: "250",
        },
        {
          id: "date",
          label: "建立日期(開始~結束日期)",
        },
        {
          id: "dutyPerson",
          label: "廠商施工負責人/安衛人員",
        },
      ],
    };
  },
  computed: {
    allCourseList() {
      //return this.$store.state.allCourseList;
      var vm = this;
      if (
        vm.allCourseData &&
        vm.allCourseData.data &&
        vm.allCourseData.data.isOK
      ) {
        return vm.allCourseData.data.data;
      }
      return null;
    },
    allPersonListForDuty() {
      //return this.$store.state.allPersonListForDuty;
      var vm = this;
      if (
        vm.allPersonListForDutyData &&
        vm.allPersonListForDutyData.data &&
        vm.allPersonListForDutyData.data.isOK
      ) {
        return vm.allPersonListForDutyData.data.data;
      }
      return null;
    },
    dailyProjectList() {
      var vm = this;
      /*if (!vm.$store.state.dailyProjectList) return;
      vm.$store.state.dailyProjectList.forEach((e) => {
        e.projectName = `${e.projectNo}\n(${e.projectName})`;
        e.date = `${e.applyDate}\n(${e.beginTime} ~ ${e.endTime})`;
        e.dutyPerson = `${e.dutyName}\n(${e.dutyId})`;
      });
      return vm.$store.state.dailyProjectList;*/
      if (
        vm.dailyProjectData &&
        vm.dailyProjectData.data &&
        vm.dailyProjectData.data.isOK
      ) {
        var tempData = JSON.parse(
          JSON.stringify(vm.dailyProjectData.data.data)
        );
        tempData.forEach((e, index) => (e.index = index + 1));
        tempData = tempData.slice(0, 10);
        tempData.forEach((e) => {
          e.projectName = `${e.projectNo}\n(${e.projectName})`;
          e.date = `${e.applyDate}\n(${e.beginTime} ~ ${e.endTime})`;
          e.dutyPerson = `${e.dutyName}\n(${e.dutyId})`;
        });
        return tempData;
      }
      return null;
    },
  },
  methods: {
    changeSelection(item) {
      var vm = this;
      vm.selectedData = item;
    },
    changePage(page) {
      var vm = this;
      vm.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsListName: "projectList",
        paramsDataName: "projectListData",
      });
    },
    async onQueryClick() {
      var vm = this;
      const courseId = vm.formQuery.course
        ? vm.formQuery.course.courseId
        : null;
      const applyPk = vm.formQuery.applyPk;

      vm.$refs["formQuery"].validate(async (valid) => {
        if (!valid) {
          if (!courseId) {
            vm.$message({
              type: "warning",
              showClose: true,
              message: "請先選取承攬商入廠施工群組!",
            });
            return;
          }
          if (!applyPk) {
            vm.$message({
              type: "warning",
              showClose: true,
              message: "請輸入申請單號條件!",
            });
            return;
          }
          return;
        }
        vm.qryBtnDisabled = true; //停用查詢按鈕
        //清空下方清單
        vm.resetSendForm();
        //取得 廠商施工負責人/安衛人員
        vm.allPersonListForDutyData = await this.$store.dispatch(
          "getAllPersonListForDuty",
          courseId
        );
        //查詢 列出符合條件且尚未入廠核銷的單據列表
        vm.dailyProjectData = await this.$store.dispatch(
          "getDailyProjectList",
          {
            courseId,
            applyPk,
          }
        );
        vm.qryBtnDisabled = false; //啟用按鈕
        if (vm.dailyProjectList.length == 0) {
          vm.$message({
            type: "warning",
            showClose: true,
            message: "查無資料!",
          });
          return;
        }
      });
    },
    async submitForm() {
      var vm = this;
      vm.$refs["formSend"].validate(async (valid) => {
        if (!valid) {
          if (
            !vm.formSend.chgDutyPerson ||
            (vm.formSend.chgDutyPerson && !vm.formSend.chgDutyPerson.id)
          ) {
            vm.$message({
              type: "warning",
              showClose: true,
              message: "所選人員空白,請選擇人員!",
            });
            return;
          }
          return;
        }
        vm.updateBtnDisabled = true; //停用送出按鈕
        vm.formSend.selectedData = vm.selectedData.map((e) => {
          return { id: vm.formSend.chgDutyPerson.id, applyPk: e.applyPk };
        });
        //console.log("預計將發送的資料");
        //console.log(vm.formSend);
        const res = await this.$store.dispatch(
          "chgDutyPerson",
          vm.formSend.selectedData
        );
        if (res.data.isOK) {
          //重新查詢
          await vm.onQueryClick();
          vm.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "變更成功",
          });
        }
        vm.updateBtnDisabled = false; //啟用按鈕
      });
    },
    resetSendForm() {
      var vm = this;
      //清空清單資料
      //vm.$store.state.dailyProjectList = null;
      vm.dailyProjectData = null;
      //清單選取清單資料
      vm.selectedData = null;
      //當 <el-form-item> 元素被隱藏時，使用 resetFields() 方法無法重置其表單值
      //if (vm.$refs["formSend"]) {
      //  vm.$refs["formSend"].resetFields();
      //} else {
      vm.formSend.selectedData = {};
      vm.formSend.chgDutyPerson = null;
      //}
    },
  },
  //頁面初始後的階段
  async mounted() {
    var vm = this;
    //頁面初始化，當 <el-form-item> 元素被隱藏時，要手動重置其表單值
    vm.resetSendForm();

    //取得所有承攬商入廠施工群組
    vm.allCourseData = await vm.$store.dispatch("getAllCourseList");

    //頁面載入之後，打開上方 Header 與 左側 SideBar
    vm.$store.commit("HEADER_HANDLER", true);
    vm.$store.commit("BREADCRUMB_HANDLER", true);
    vm.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./staffChange.html"></template>
<style lang="sass" scoped src="../../../public/styles/custom.sass"></style>
